      user_name: "",
      friend: -1,
      friend_list: [
        {
          text: "全部",
          value: -1
        },
        {
          text: "文本",
          value: 0
        },
        {
          text: "图片",
          value: 1
        },
        {
          text: "音频",
          value: 2
        }
      ],
      friend_data_list: [],
      loading: false,
      loading1: false,
      text_list: true,
      page: 0,
      // 传递的订单数据
      orderData: {
        user_id: 0,
        user_name: "",
        user_phone: "",
        doctor_id: 0,
        doctor_name: "",
