      // type	Int	<可选参数>用户ID
    };
  },
  mounted() {
    this.orderData = this.$route.query.data;
    this.info_list();
  },
  methods: {
    // eslint 容错
    eslint() {},
    //   好友列表
    // info() {
    //   this.info_list();
    //   this.user_name =
    //     this.$route.query.user_data.user_name == undefined
    //       ? this.$route.query.user_data.name
    //       : this.$route.query.user_data.user_name;
    //   this.$axios({
    //     method: "post",
    //     url: this.$apis.URL.user_friend_list,
    //     data: {
    //       user_id:
    //         this.$route.query.user_data.id == undefined
    //           ? this.$route.query.user_data.doctor_id
    //           : this.$route.query.user_data.id
    //     }
    //   })
    //     .then(response => {
    //       if (response.data.result === 0) {
    //         this.friend_data_list = response.data.datas.list;
    //         this.friend_data_list.forEach(item => {
    //           this.friend_list.push(item.friend_name);
    //         });
    //         this.friend = this.friend_list[0];
    //         this.info_list();
    //       } else {
    //         this.alert.snackbar = true;
    //         this.alert.text = response.data.message;
    //       }
    //     })
    //     .catch(error => {
    //       this.alert.snackbar = true;
    //       this.alert.text = error;
    //     });
    // },

    // 聊天记录列表
    info_list(state) {  
      if (state == "updata") {
        this.page = this.page + 1;
      } else {
        this.data_ = [];
      }
      this.loading = true;
      // let begin_D =
      //   Date.parse(`${this.date}  ${this.time}`) / 1000 - 24 * 60 * 60;
      // let end_D =
      //   Date.parse(`${this.date1}  ${this.time1}`) / 1000 + 24 * 60 * 60;

      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_messages,
        data: {
          // start_time: begin_D,
          // end_time: end_D,
          page_count: 100,
          page: this.page,
          type: this.friend,
          msg_index: this.$route.query.data.msg_index
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.data_.push(...response.data.datas.list);
            // console.log(this.data_);
            // let user__ = {
            //   avatar: "",
            //   friend_name: ""
            // };
            // this.friend_data_list.forEach(item => {
            //   if (friend_ == item.friend_id) {
            //     user__.avatar = item.avatar;
            //     user__.friend_name = item.friend_name;
            //     return false;
            //   }
            // });
            // this.data_.push(...response.data.datas.list);
            // let user_ = {
            //   avatar: "",
            //   friend_name: ""
            // };
            // let route_ = this.$route.query.user_data;
            // this.data_.forEach(item => {
            //   if (friend_ == item.from_id) {
            //     item.user__ = user__;
            //   }
            //   if (
            //     (route_.id == undefined ? route_.doctor_id : route_.id) ==
            //     item.from_id
            //   ) {
            //     user_.avatar = route_.thumb_url;
            //     user_.friend_name =
            //       route_.user_name == undefined
            //         ? route_.name
            //         : route_.user_name;
            //     item.user__ = user_;
            //   }
            // });
            // // alert( response.data.datas.list.length );
            if (response.data.datas.list.length < 100) {
              this.text_list = false;
            } else {
              this.text_list = true;
            }
          } else {
            this.$Dialog.getProductCheckResult_({
                text: response.data.message,
                timeout: 3000,
                snackbar: true
            })
          }
          setTimeout(() => {
            // 初始化音频
            window.RongIMLib.RongIMVoice.init();
            this.loading = false;
          }, 500);
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
                text: error,
                timeout: 3000,
                snackbar: true
            })
        });
    },
    // 播放音频
    audio_play(base64, msg_id) {
      this.msg_id = msg_id;
      let time = -~(base64.length / 2100);
      this.loading1 = true;
      this.Timeout = setTimeout(() => {
        this.loading1 = false;
        this.msg_id = false;
        clearTimeout(this.Timeout);
      }, (time + 3) * 1000); // 播放音频
      window.RongIMLib.RongIMVoice.play(base64);
    },
    // 停止播放
    stop_play() {
      clearTimeout(this.Timeout);
      this.loading1 = false;
      window.RongIMLib.RongIMVoice.stop();
      this.msg_id = false;
    },
    /** 图片预览 */
    img_(img) {
      window.open(
        img,
        "newwindow",
        `height=600, width=800, top=${document.body.clientHeight / 2 -
          300}, left=${document.body.clientWidth / 2 -
          400}, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=no, status=no`
      );
    }
    // // 开始日期
    // begin_date(date) {
    //   this.menu = false;
    //   this.$refs.menu.save(date);
    //   this.menu1 = true;
    //   this.info_list();
    // },
    // // 开始时间
    // begin_time(date) {
    //   this.menu1 = false;
    //   this.$refs.menu1.save(date);
    //   this.info_list();
    // },
    // // 结束日期
    // end_date(date) {
    //   this.menu2 = false;
    //   this.$refs.menu2.save(date);
    //   this.menu3 = true;
    //   this.info_list();
    // },
    // // 结束时间
    // end_time(date) {
    //   this.menu3 = false;
    //   this.$refs.menu3.save(date);
    //   this.info_list();
    // }
  }
};
</script>

<style lang="less">
#chat_ {
  .v-list.v-list--two-line.theme--light {
    height: calc( 100vh - 220px );
    overflow-y: auto;
  }
  .v-list--two-line .v-list__tile {
    height: auto !important;
    min-height: 90px;
  }
  .v-list__tile__title {
    height: auto !important;
  }
  .grey {
    border: solid 1px #000;
  }
  .img__ {
    max-width: 340px;
    border: dashed 1px #000;
    border-radius: 5px;
    padding: 5px;
  }
  .v-avatar .v-icon {
    font-size: 50px;
  }
  .v-list.v-list--two-line.theme--light > div{
    border-bottom: solid 1px #e6e6e6;
  }
}
</style>
